.menu {
  width: 300px;
  height: calc(100vh - 100px);
  position: absolute;
  padding: 32px;
  box-sizing: border-box;
  background-color: white;
  top: 32px;
  display: flex;
  transition: all 0.5s linear;

  right: -500px;
  flex-direction: column;
  justify-content: space-between;
}
.menu_active {
  right: -20px;
}
.menu__enter-button {
  padding: 12px 40px;
  background: #2f80ed;
  border-radius: 58px;
  width: 128px;
  margin-bottom: 40px;
  height: 43px;
  box-sizing: border-box;
  text-decoration: none;
  color: white;
}
.menu__link {
  font-size: 20px;
  line-height: 24px;
  color: #2c415d;
  text-decoration: none;
  margin-bottom: 32px;
}
.menu__top {
  display: flex;
  flex-direction: column;
}
.menu__bottom {
  display: flex;
  gap: 30px;
}
.menu__profile-container {
  display: flex;
  align-items: center;
  text-decoration: none;
  margin-bottom: 40px;
}
.menu__profile-image {
  width: 44px;
  height: 44px;
  margin-right: 12px;
}
.menu__profile-name {
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: #2c415d;
  margin: 0;
}
.menu__container {
  display: flex;
  justify-content: space-between;
}
.menu__close-button {
  width: 40px;
  height: 40px;
}
